/* PrismJS Theme */
:root {
    --prism-bg: #ffffff;
    --prism-text: #24292e;
    --prism-comment: #6a737d;
    --prism-keyword: #d73a49;
    --prism-string: #032f62;
    --prism-function: #6f42c1;
    --prism-number: #005cc5;
    --prism-operator: #d73a49;
    --prism-class: #6f42c1;
}

.dark {
    --prism-bg: #1e1e1e;
    --prism-text: #d4d4d4;
    --prism-comment: #6a9955;
    --prism-keyword: #c586c0;
    --prism-string: #ce9178;
    --prism-function: #dcdcaa;
    --prism-number: #b5cea8;
    --prism-operator: #d4d4d4;
    --prism-class: #4ec9b0;
}

code[class*="language-"],
pre[class*="language-"] {
    color: var(--prism-text);
    background: var(--prism-bg) !important;
    font-family: "JetBrains Mono", ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !important;
    font-size: 13px !important;
    line-height: 1.6 !important;
    direction: ltr;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    -moz-tab-size: 4;
      -o-tab-size: 4;
         tab-size: 4;
    hyphens: none;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color: var(--prism-comment);
    font-style: italic;
}

.token.punctuation {
    color: var(--prism-text);
    opacity: 0.7;
}

.token.namespace {
    opacity: .7;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
    color: var(--prism-number);
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
    color: var(--prism-string);
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
    color: var(--prism-operator);
}

.token.atrule,
.token.attr-value,
.token.keyword {
