/* Browser Tab Shoulder Style */
.tab-active {
    position: relative;
    z-index: 30;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 -4px 12px -2px rgba(0, 0, 0, 0.03);
    /* Subtle top lift */
}

.dark .tab-active {
    box-shadow: 0 -4px 12px -2px rgba(0, 0, 0, 0.2);
}

.tab-active::before,
.tab-active::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 20px;
    height: 20px;
    pointer-events: none;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.tab-active::before {
    left: -20px;
    border-bottom-right-radius: 18px;
    box-shadow: 10px 0 0 0 white;
}

.tab-active::after {
    right: -20px;
    border-bottom-left-radius: 18px;
    box-shadow: -10px 0 0 0 white;
}

.dark .tab-active::before {
    box-shadow: 10px 0 0 0 #212121;
}

.dark .tab-active::after {
    box-shadow: -10px 0 0 0 #212121;
}

/* Background Tab Depth */
.tab-inactive {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    background-color: #f7f7f7;
    margin-bottom: 2px;
}

.dark .tab-inactive {
    background-color: #1a1a1a;
}

/* The shadow requested by the user for background tabs to look "in the back" */
.tab-inactive-shadow {
    position: absolute;
    inset: 0;
    pointer-events: none;
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.02);
    border-radius: 12px 12px 0 0;
}

.dark .tab-inactive-shadow {
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.tab-dragging {
    opacity: 0.5;
    cursor: grabbing;
}

